







































































































































import Vue from "vue";

import RomaCrud from './RomaCrud.vue'

export default Vue.extend({
    name: "RomaPurpose",
    props: {
        value: { // Unidad
            type: Object,
        },
        school: {
            type: Object,
            default () {
                return this.$store.state.organizer.selectedSchool;
            }
        }
    },
    data() {
        return {
            dialog: false,
            unit: null,
            valid: false,
            masterSelected: 'preLearning',
            request: {
                description: '',
                preLearning: [],
                skills: [],
                attitudes: [],
            },
            cruds: {
                preLearning: {
                    title: 'Maestro de Aprendizajes',
                },
                skills: {
                    title: 'Maestro de Habilidades',
                },
            },
        };
    },
    computed: {
      crud () {
          return {
              headers: [
                  { text: 'Id', value: 'id', dense: true },
                  { text: 'Texto', value: 'text', editable: true },
                  { text: 'Acciones', value: 'action', align: 'right', sortable: false }
              ],
              defaultItem: {id: null, text: ''},
              data: this.$store.state.masters[this.masterSelected],
              onSave: (item: object) => {
                  const msj = {
                      show: true,
                      text: 'Maestro guardado correctamente!',
                      color: 'success',
                  }

                  this.$store.state.snackbar = msj;
                  this.$store.commit('setMaster', {id: this.masterSelected, item: item});
              },
              onDelete: (item: object) => {
                  this.$store.commit('delMaster', {id: this.masterSelected, item: item});
              }
          }
      }
    },
    methods: {
      savePurpose () {
        const msj = {
            show: true,
            text: 'Propsito de la unidad Guardado!',
            color: 'success',
        }

        this.$store.state.snackbar = msj
      },
      selectMaster (index: string) {
          this.masterSelected = index;
          this.dialog = true;
      }
    },
    components: {
        RomaCrud
    }
});
